@import url(https://fonts.googleapis.com/css2?family=Freckle+Face&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bahianita&display=swap);

:root {
  --h3-color: #a7c5d8;
  --main-background: #000;
}

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(../../static/media/MaterialIcons-Regular.4674f8de.eot); /* For IE6-8 */
  src: local('Material Icons'),
    local('MaterialIcons-Regular'),
    url(../../static/media/MaterialIcons-Regular.cff684e5.woff2) format('woff2'),
    url(../../static/media/MaterialIcons-Regular.83bebaf3.woff) format('woff'),
    url(../../static/media/MaterialIcons-Regular.5e7382c6.ttf) format('truetype');
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}

