html {
  background: var(--main-background);
  min-height: 100%;
  font-size: 16px;
  color: #e4e7ea;
}
html, button {
  font-family: Karla, sans-serif;
}

body {
  margin: 0;
  padding: 0;
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
}

#appVersion {
  position: fixed;
  right: 5px;
  bottom: 3px;
  color: rgba(0, 0, 0, 0.3);
  font-size: 12px;
}

a {
  color: #5ba3e2;
  text-decoration: none;
}

pre {
  -webkit-user-select: text;
     -moz-user-select: text;
      -ms-user-select: text;
          user-select: text;
}

@-webkit-keyframes focused-element {
  0% { outline-color: rgba(255, 255, 255, 0); }
  100% { outline-color: rgba(255, 255, 255, 0.5); }
}

@keyframes focused-element {
  0% { outline-color: rgba(255, 255, 255, 0); }
  100% { outline-color: rgba(255, 255, 255, 0.5); }
}

*:focus {
  outline: 1px solid rgba(0, 0, 0, 0);
  -webkit-animation-name: focused-element;
          animation-name: focused-element;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

input[type="number"], input[type="text"], textarea {
  background: none;
  border: none;
  border-bottom: rgba(255, 255, 255, .2) 2px solid;
  box-sizing: border-box;
  color: #bcc9e2;
  font-family: Karla, sans-serif;
  font-size: 16px;
}
input[type="number"], input[type="text"] {
  height: 21px;
  margin: 2px 0;
}
textarea {
  border: rgba(255, 255, 255, .2) 2px solid;
}

@-webkit-keyframes focused-input-invalid {
  0% { border-color: rgba(255, 0, 0, 0.1); }
  100% { border-color: rgba(255, 0, 0, 0.3); }
}

@keyframes focused-input-invalid {
  0% { border-color: rgba(255, 0, 0, 0.1); }
  100% { border-color: rgba(255, 0, 0, 0.3); }
}
input[type="number"]:focus.InvalidInput, input[type="text"]:focus.InvalidInput, input:focus.InvalidInput {
  -webkit-animation-name: focused-input-invalid;
          animation-name: focused-input-invalid;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}
@-webkit-keyframes focused-input {
  0% { border-color: rgba(255, 255, 255, 0.1); }
  100% { border-color: rgba(255, 255, 255, 0.3); }
}
@keyframes focused-input {
  0% { border-color: rgba(255, 255, 255, 0.1); }
  100% { border-color: rgba(255, 255, 255, 0.3); }
}
input[type="number"]:focus, input[type="text"]:focus, input:focus {
  -webkit-animation-name: focused-input;
          animation-name: focused-input;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

h1, h2, h3, h4, .Header1, .Header2, .Header3 .Header4,
input[type="text"].Header1, input[type="text"].Header2, input[type="text"].Header3, input[type="text"].Header4 {
  color: #ffffff;
}

h1, .Header1, input[type="text"].Header1 {
  font-size: 60px;
  font-family: 'Bahianita', cursive;
  margin-top: 10px;
  margin-bottom: 5px;
  font-weight: normal;
}

h2, .Header2, input[type="text"].Header2 {
  font-size: 50px;
  font-family: 'Bahianita', cursive;
  margin-top: 30px;
  margin-bottom: 5px;
  font-weight: normal;
}

h3, .Header3, input[type="text"].Header3 {
  font-size: 30px;
  font-family: 'Bahianita', cursive;
  margin-top: 10px;
  margin-bottom: 5px;
  font-weight: normal;
}

small {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.5);
}
h1 small, h2 small, h3 small {
  color: rgb(200, 200, 200);
}
h1 a, h2 a, h3 a, h4 a {
  text-decoration: none;
}

.firebaseui-subtitle, .firebaseui-title {
  color: var(--h3-color);
  text-align: center;
}

.Dimmed {
  opacity: 0.5;
}

.CodeMirror {
  font-size: 12px;
}

.OneLineEllipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
}

::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.5);
  border-radius: 6px;
}

th {
  text-align: left;
}

